import React, { useState, useCallback, useEffect } from 'react';
import Sessions from './Sessions';
import SessionDetails from './SessionDetails';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import IconBack from '@material-ui/icons/ArrowBack';
import IconPerson from '@material-ui/icons/Person';
import styles from './Main.module.css';
import { User, SessionData } from './Constants';

interface Props {
    user: User;
}
// enum Mode {
//     Sessions = 'Sessions',
//     SessionDetails = 'Session Details',
// }

const Main: React.FunctionComponent<Props> = (p: Props) =>
{
    const [data, setData] = useState<SessionData[] | undefined>(undefined);

    useEffect(() => {
        const processData = (d: SessionData[]) =>
        {
            d.forEach((it: any) =>
            {
                let date = new Date(it.dateCollected);
                it.date = +date;
                it.dateString = date.toDateString();
                it.rankId = 0;
                it.rank = 'Unknown Rank';
                it.name = `${it.lastName}, ${it.firstName}`;
            });
            console.log(d);
            setData(d);
        }
        let summary = localStorage.getItem('_test_summary');
        if (summary)
        {
            processData(JSON.parse(summary));
        }
        else
        {
            fetch('https://eyenetservice20190228013441.azurewebsites.net/v1/result/summary', {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + p.user.token
                }
            }).then(async (result) => {
                let data = await result.json();

                localStorage.setItem('_test_summary', JSON.stringify(data));
                processData(data);
            });
        }
    }, [ p.user.token ]);

    const [selectedSession, setSelectedSession] = useState<SessionData | undefined>(undefined);
    const goBack = useCallback(() => setSelectedSession(undefined), []);

    return (
        <div className={styles.container}>
            <AppBar position="static" className={styles.appBar}>
                <Toolbar>
                    {selectedSession && (
                        <IconButton className={styles.menuButton} color="inherit" aria-label="Menu">
                            <IconBack onClick={goBack} />
                        </IconButton>
                    )}
                    <Typography variant="h6" color="inherit">
                        {selectedSession ? 'Session Details' : 'Sessions'}
                    </Typography>
                    <div className={styles.grow} />
                    <Button className={styles.menuButton} color="inherit" aria-label="Menu">
                        <IconPerson className={styles.person} />
                        User
                    </Button>
                </Toolbar>
            </AppBar>
            <div className={styles.main}>
                {!data && <span>Loading...</span>}
                {selectedSession && <SessionDetails user={p.user} session={selectedSession} />}
                {!selectedSession && data && <Sessions user={p.user} data={data} onSelectSession={setSelectedSession} />}
            </div>
        </div>
    );
}

export default Main;