import React, { useState, useCallback, useEffect } from 'react';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import IconArrowDown from '@material-ui/icons/KeyboardArrowDown';
import IconArrowRight from '@material-ui/icons/KeyboardArrowRight';
import IconCheckBox from '@material-ui/icons/CheckBoxOutlineBlank';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import styles from './Sessions.module.css';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import { FakeDataSessions } from './FakeData';
import { SessionData } from './Constants';

interface Props {
    user: any;
    data: SessionData[];
    onSelectSession: (session: SessionData) => void;
}
interface SessionInfo {
    platforms: { [key: number]: string };
    ranks: { [key: number]: string };
    dates: { [key: number]: string };
    subjects: { [key: number]: string };
}

enum Mode {
    Name = 'Name',
    Rank = 'Rank',
    Platform = 'Platform',
    Date = 'Date',
}

const getSortKey = (mode: string) =>
{
    return (mode === Mode.Name && 'subjects') ||
           (mode === Mode.Rank && 'ranks') ||
           (mode === Mode.Platform && 'platforms') ||
           (mode === Mode.Date && 'dates') || '';
};

const sort = (data: SessionData[], mode: Mode, search: string, rank: number, platformId: number, date: number) =>
{
    let groups: { [key: string]: SessionData[] } = {},
        sortKey = getSortKey(mode);

    data.forEach((item) =>
    {
        if ( // (rank === 'All' || item.rank === rank) &&
            (platformId < 0 || item.platformId === platformId) &&
            (date < 0 || item.date === date) &&
            (!search || (item as any)[sortKey].includes(search)))
        {
            let sortVal = (item as any)[sortKey];
            if (!groups[sortVal])
            {
                groups[sortVal] = [];
            }
            groups[sortVal].push(item);

        }
    });

    let keysSorted = Object.keys(groups).sort(),
        out = keysSorted.map(id => groups[id]);

    return out;
};

const addTab = (name: string, mode: string) =>
{
    return <Tab key={name} label={name} value={name} className={styles.tab} />
};

const addMenuItem = ({ id, name }: { id: number, name: string }) =>
{
    return <MenuItem key={id} value={id} className={styles.menuItem}>{name}</MenuItem>
};

const addSelect = (name: string, currentValue: number, setValue: (value: number) => void, values: { [key: number]: string }) =>
{
    const onChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        setValue(+e.target.value);
    };

    let arr = Object.keys(values)
                    .map(key => ({ id: +key, name: values[+key] }))
                    .sort((a, b) => a.name.localeCompare(b.name));

    return (
        <div className={styles.selectWrapper}>
            <InputLabel className={styles.inputLabel} htmlFor={name}>{name}</InputLabel>
            <Select
                className={styles.select}
                value={currentValue}
                onChange={onChange}
                inputProps={{
                    name: name,
                    id: name,
                }}
            >
                {arr.map(addMenuItem)}
            </Select>
        </div>
    );
};

const processData = (data: SessionData[]) =>
{
    let out: SessionInfo = {
        platforms: {},
        ranks: {},
        dates: {},
        subjects: {}
    }

    data.forEach(session => {
        out.platforms[session.platformId] = session.platformName;
        out.dates[session.date] = session.dateString;
        out.subjects[session.subjectId] = session.name;
        out.ranks[session.rankId] = session.rank;
    });


    return out;
}

const renderHeader = (first: SessionData, mode: Mode, isExpanded: boolean, expand: () => void) =>
{
    let content;
    if (mode === Mode.Name)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{`${first.lastName}, ${first.firstName}`}</div>
                <div className={styles.rank}>{first.rank}</div>
            </>
        );
    }
    else if (mode === Mode.Rank)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.rank}</div>
            </>
        );
    }
    else if (mode === Mode.Platform)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.platformName}</div>
            </>
        );
    }
    else if (mode === Mode.Date)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.dateString}</div>
            </>
        );
    }

    return (
        <div key={'h' + first.resultId} className={`${styles.row} ${styles.rowGroup}`}>
            <IconButton className={styles.menuButton} color="inherit" aria-label="Menu" onClick={expand}>
                {isExpanded ? <IconArrowDown /> : <IconArrowRight />}
            </IconButton>
            {content}
        </div>
    );
}

const Sessions: React.FunctionComponent<Props> = (p: Props) =>
{
    const [ search, setSearch ] = useState('');
    const [ mode, setMode ] = useState(Mode.Name);
    const [ rank, setRank ] = useState(-1);
    const [ platform, setPlatform ] = useState(-1);
    const [ date, setDate ] = useState(-1);
    const [ expanded, setExpanded ] = useState<any>({});

    const onChangeTab = useCallback((e: any, value: any) => {
        setMode(value);
    }, []);

    const onChangeSearch = useCallback((e: any) => {
        setSearch(e.target.value);
    }, []);

    const onClickExpand = useCallback((key: any) => {
        let newVal = Object.assign({}, expanded);
        newVal[key] = !newVal[key];

        setExpanded(newVal);
    }, [ expanded ]);

    const [ sessionInfo, setSessionInfo ] = useState(() => processData(p.data));

    let groups = sort(p.data, mode, search, rank, platform, date);

    return (
        <div className={styles.sessions}>
            <Tabs className={styles.tabs} value={mode} onChange={onChangeTab}>
                {addTab(Mode.Name, mode)}
                {addTab(Mode.Rank, mode)}
                {addTab(Mode.Platform, mode)}
                {addTab(Mode.Date, mode)}
            </Tabs>
            <Paper>
                <div>
                    <div className={`${styles.row} ${styles.headerRow}`}>
                        <TextField
                            label={mode}
                            variant="outlined"
                            margin="dense"
                            onChange={onChangeSearch}
                            InputProps={{ classes: { input: 'textField' } }}
                        />
                        {addSelect('Rank', rank, setRank, sessionInfo.ranks)}
                        {addSelect('Platform', platform, setPlatform, sessionInfo.platforms)}
                        {addSelect('Date', date, setDate, sessionInfo.dates)}
                    </div>
                    <div>
                        {groups.map((group: SessionData[]) => {
                            let first = group[0],
                                key = (first as any)[getSortKey(mode)],
                                isExpanded = expanded[key],
                                expand = () => onClickExpand(key);

                            return (
                                <React.Fragment key={first.resultId}>
                                    {renderHeader(first, mode, isExpanded, expand)}
                                    {isExpanded && group.map(row => (
                                        <div key={row.resultId} className={`${styles.row} ${styles.rowItem}`}>
                                            <IconButton className={styles.menuButton} color="inherit" aria-label="Menu">
                                                <IconCheckBox />
                                            </IconButton>
                                            <div className={styles.nowrap} onClick={() => p.onSelectSession(row)}>
                                                <div className={styles.platform}>{row.testName}</div>
                                                <div className={styles.date}>{row.platformName}</div>
                                                <div className={styles.date}>{row.dateString}</div>
                                            </div>
                                        </div>
                                    ))}
                                </React.Fragment>
                            );
                        })}
                    </div>
                </div>
            </Paper>
        </div>
    );
}

export default Sessions;